@charset "utf-8";


.todo_item{
  padding: 1rem;
  display: flex;
  align-items: center;
  cursor: pointer;
  line-height: 40px;
}
.todo_item{background-color: #ccc;}
.todo_item:hover .remove{
  opacity: 1;
}

.todo_item + .todo_item{
  border-top: 1px solid #f1f3f5;
}
/* 삭제버튼 서식 X */

.remove{
  opacity: 0;
  font-weight: bold;
  color: #f00;
  margin-right: 0.5rem;
}

.todo_text{
  flex:1;
  word-break: break-all;
}

.checked{
  text-decoration: line-through;
  color: #adb5bd;
}

.check_mark{
  font-size: 1.5rem;
  line-height: normal;
  color: #f00;
  font-weight: bold;
}
