@charset "utf-8";

.form{
  display: flex;
  padding: 1rem;
}

.form input{
  flex:1; /* 버튼을 뺀 빈 공간을 모두 채워준다.*/
  font-size: 1.25rem;
  outline: none;
  border: none;
  border-bottom: 1px solid #c5f6fa;
}

.create_btn{
  padding: 0.5rem 1rem;
  margin-left: 1rem;
  background-color: #22b8cf;
  border-radius: 3px;
  color: white;
  font-weight: 600;
  border: none;
  cursor: pointer;
}

.create_btn:hover{
  background: #3bc9db;
}